/**
 * Componente para cargar las texturas de los modelos de los personajes
 */

// *************************************** CARGA DE IMÁGENES DE TEXTURAS ***************************************
const ojos_color_image = require('../../../assets/models/characters/Texturas/Hombre/Ojos/Ojos_Ojos_Color_BaseColor.png');
const cejas_H_image = require("../../../assets/models/characters/Texturas/Hombre/Cejas/Cejas_Cejas_Color_BaseColor.png");
const ojos_color_image_M = require("../../../assets/models/characters/Texturas/Mujer/Ojos/Ojos_mujer_Ojos_Color_BaseColor.png");
const cejas_H_image_M = require("../../../assets/models/characters/Texturas/Mujer/Cejas/Cejas_mujer_Cejas_Color_BaseColor.png")

// *************************************** CARGA DE TEXTURAS ***************************************

const ojos_color_texture = new THREE.TextureLoader().load( ojos_color_image );
ojos_color_texture.flipY = false;

const cejas_H_texture = new THREE.TextureLoader().load( cejas_H_image );
cejas_H_texture.flipY = false;

const ojos_color_texture_M = new THREE.TextureLoader().load( ojos_color_image_M );
ojos_color_texture_M.flipY = false;

const cejas_H_texture_M = new THREE.TextureLoader().load( cejas_H_image_M );
cejas_H_texture_M.flipY = false;


// Componente AFRAME para cargar las texturas predefinidas de cejas y ojos
AFRAME.registerComponent('character-textures', {

    schema: {
        avatarType: {type: 'string', default: 'hombre'}
    },

    init: function(){
        let el = this.el;
        const avatarType = this.data.avatarType;
        // console.log('data', avatarType);

        el.addEventListener("model-loaded", e =>{
            let mesh = el.getObject3D('mesh');
            if (!mesh){return;} 

            mesh.traverse(function(node){
                
                if (node.isMesh){                           
                    switch(node.material.name) {
                        
                        case "Ojos_Color":
                            // console.log(node.material);
                            node.material.map = avatarType === 'hombre'? ojos_color_texture : ojos_color_texture_M;
                            node.material.transparent = true;
                            // node.material.side = THREE.DoubleSide;
                            break;
                        case "Cejas_Color":
                            node.material.map = avatarType === 'hombre'? cejas_H_texture : cejas_H_texture_M;
                            node.material.transparent = true;
                            // node.material = ojos_H_material;
                            break;
    
                    }


                }
            });

        });
    }
    
});