import React from 'react';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

//icons
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import AddCircleOutlineRoundedIcon from '@mui/icons-material/AddCircleOutlineRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';


export default function MainProductos(props) {


    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "13%",

        padding: '2rem 0.5rem',
    }

    const myStyle = {
        display: "flex",
        height: "100%",
        width: "33%",
        padding: '0rem 0.5rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyle2 = {
        display: "flex",
        height: "100%",
        width: "33%",
        flexDirection: "column",
        padding: '0.5rem 1rem'
    }

    const pestanaStyle = {
        width: "7rem",
        height: "100%",
        backgroundColor: "lightGray",
        marginRight: "0.5rem",
        borderRadius: "0px 0px 5px 5px"
    }


    return (


        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
            <Box sx={backDivStyle}>
                <button style={styleButton} onClick={()=>props.handleClickBack(props.arrobject.clave_gondola_tipo_1)}>
                    <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
                    <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
                </button>


            </Box>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Box sx={myStyle}>
                {/* No tienes productos configurados
                <AddCircleOutlineRoundedIcon sx={{ mr: 1, color: "white" }} /> */}
                <Typography sx={{ color: 'white' }}>Lista de productos</Typography>
                
            </Box>
            
        </Box>


    )
}