import axios from "axios";
import addLogo from '../assets/images/logos_loacles/addLogo.jpg'

export const updateShelf = (obj) => {
    const url = "https://p297hirw91.execute-api.us-east-1.amazonaws.com/v1/"

    let colorCuerpo = getColor(obj.clave_gondola_tipo_1,"Estantes_Color")
    let colorBorde = getColor(obj.clave_gondola_tipo_1,"Borde_Estantes")
    //console.log(colorCuerpo)
    let logo_superior1 = getLogos(obj.clave_gondola_tipo_1, "superior1-")
    let logo_superior2 = getLogos(obj.clave_gondola_tipo_1, "superior2-")
    let logo_superior3 = getLogos(obj.clave_gondola_tipo_1, "superior3-")
    let logo_superior4 = getLogos(obj.clave_gondola_tipo_1, "superior4-")
    console.log(logo_superior1)

    const bodyUpdate = {
        "request_data": {
          "type_action": "update",
          "data": {
                      "id_gondola_tipo_1": obj.id_gondola_tipo_1,
                      "color_borde_gondola_tipo_1": colorBorde,
                      "id_local_gondola_tipo_1": obj.id_local_gondola_tipo_1,
                      "logo_superior1_gondola": "",
                      "logo_superior2_gondola": "",
                      "logo_superior3_gondola": "",
                      "logo_superior4_gondola": "",
                      "banner_superior1_gondola": "",
                      "banner_superior2_gondola": "",
                      "ubicacion_gondola_tipo_1": obj.ubicacion_gondola_tipo_1,
                      "color_cuerpo_gondola_tipo_1": colorCuerpo,
                      "clave_gondola_tipo_1": obj.clave_gondola_tipo_1
                  }
        },
        "headers": {
          "Content-Type": "application/json",
          "Accept": "application/json",
          "Authorization": window.localStorage.getItem('adminToken')
        }
      }

    // Axios
    axios.post(url + 'gondolas', bodyUpdate, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
        .then(response => {
            console.log('respuesta :', response)

            if (response.status == '200' && response.data.status != 'failed') {
                alert("Actualizado correctamente en la base de datos.")  

            }
        })
        .catch(error => {
            console.log('error : ', error)
        });
}

const getColor = (clave, meshMat) =>{

    let modelShelf = document.getElementById('shelf'+clave)
    let mesh = modelShelf.object3D
    let mycolor

        mesh.traverse(function(node){
            if (node.isMesh){  
                //console.log(node.material.name)
                if(node.material.name===meshMat){
                    //console.log(node)
                    mycolor = '#'+ (node.material.color).getHexString()
                           
                }
            
            }
        })
    return mycolor
}

const getLogos = (clave, pos) =>{

    let imagen = document.getElementById(""+pos+clave).getAttribute('src')
    if(addLogo === imagen){
        imagen = null
    }

    return imagen

}
