import React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import CategoryIcon from '@mui/icons-material/Category';
import { useTheme } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';

import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';
import FolderRoundedIcon from '@mui/icons-material/FolderRounded';

import { getProduct } from '../../../server/Data';

//  COMPONENTES UI
// import ShoppingCart from './ShoppingCart';

export default function ProductModal() {
    const [open, setOpen] = React.useState(false);
    const [panelEl, setPanelEl] = React.useState();
    const [nameProduct, setNameProduct] = React.useState();
    const inputEl = React.useRef(null);
    const [imageData, setImageData] = React.useState();
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    React.useEffect(() => {

        document.addEventListener('open_product_modal', function (e) {
            //console.log(e.detail.element.children);
            setPanelEl(e.detail.element)
            setImageData(e.detail.element.children[0].getAttribute('src'))
            setNameProduct(e.detail.element.getAttribute('nameProduct'))
            handleClickOpen();
        })

        document.addEventListener('load_product', function (e) {
            //console.log(e.detail.element.children);
            //setPanelEl(e.detail.element)
            setImageData(e.detail.imagen)
            setNameProduct(e.detail.nombre)
            
        })


    }, []);








    const BootstrapDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
            padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
            padding: theme.spacing(1),
        },
    }));

    const BootstrapDialogTitle = (props) => {
        const { children, onClose, ...other } = props;

        return (
            <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
                {children}
                {onClose ? (
                    <IconButton
                        aria-label="close"
                        onClick={onClose}
                        sx={{
                            position: 'absolute',
                            right: 8,
                            top: 8,
                            color: (theme) => theme.palette.grey[500],
                        }}
                    >
                        <CloseIcon />
                    </IconButton>
                ) : null}
            </DialogTitle>
        );
    };

    BootstrapDialogTitle.propTypes = {
        children: PropTypes.node,
        onClose: PropTypes.func.isRequired,
    };

    const ColorButton = styled(Button)(({ theme }) => ({
        color: "white",
        backgroundColor: "#ea660e",
        '&:hover': {
            backgroundColor: "#d65d0d",
        },
    }));

    const contentStyle = {
        minHeight: "10rem",
        width: "100%",
        display: "flex",
        justifyContent: "space-around"
    }


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleSave = () => {
        setOpen(false);
        //panelEl.children[0].setAttribute("src",imageData)
        panelEl.children[0].setAttribute("src",require('../../../assets/images/logos_loacles/hoyostools.png'))
        panelEl.setAttribute("nameProduct",nameProduct)
    }

    const handleLoadP = () => {
       // console.log(document.getElementById('outlined-required'))
        getProduct(document.getElementById('outlined-required').value)
    }

    return (
        <div>
            {/* <Button variant="outlined" className='interactable-ui' onClick={handleClickOpen}>
        Open responsive dialog
      </Button> */}
            {/* <ShoppingCart/> */}
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                fullWidth={true}
                maxWidth='sm'
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    Ingresar producto
                </BootstrapDialogTitle>
                <DialogContent dividers sx={contentStyle}>
                    <Box sx={{ width: "60%", height: "160px"}}>
                        <img src={imageData} style={{ width: "100%", height: "70%", objectFit: "contain" }}></img>
                        <Typography sx={{ textAlign: 'center', color: 'gray' }} variant='h6'>{nameProduct}</Typography>
                    </Box>
                    <Box>
                        <TextField
                            variant="filled"
                            id="outlined-required"
                            label="ref producto"
                            defaultValue=""
                        />
                        {/* <LoadingButton loading variant="outlined">Submit</LoadingButton> */}
                        <ColorButton onClick={handleLoadP}>Cargar</ColorButton>

                    </Box>
                </DialogContent>
                <DialogActions>
                    <ColorButton autoFocus onClick={handleSave} sx={{ backgroundColor: "#ea660e" }}>
                        Guardar cambio
                    </ColorButton>
                </DialogActions>
            </BootstrapDialog>
        </div>
    );
}