// Componente para cargar las preferencias de ropa y colores del avatar

import { hombreData, mujerData } from '../../../context/CharacterModelData';

AFRAME.registerComponent('load-character-prefs', {

    schema: {
        userPrefs:       {type: 'string', default: '{}'}
    },

    init: function(){
        let el = this.el;
        const userPrefs = JSON.parse(this.data.userPrefs);

        const dataTexturesExtra = userPrefs.avatar === 'hombre'? hombreData : mujerData;

        const material_textures_data = []

        //  Recorro el objeto de datos de las prendas para extraer un 
        //  arreglo sólo con materiales y texturas necesarias
        Object.keys(dataTexturesExtra.ropa).forEach(key => {
            const opcionesKey = dataTexturesExtra.ropa[key].opciones;
            // console.log(key, opcionesKey);
            opcionesKey.forEach(opcion => {
                if(opcion.texturas){
                    material_textures_data.push({
                        material: opcion.material,
                        texturas: opcion.texturas
                    });
                    
                }
            });
        });

        el.setAttribute('gltf-model', '#model-asset-personaje-' + userPrefs.avatar);
        el.setAttribute('character-textures', {avatarType: userPrefs.avatar})

        el.addEventListener("model-loaded", e =>{
            let mesh = el.getObject3D('mesh');
            if (!mesh){return;} 

            let cejas_material =null;
            let cabello_material =null;

            //  busco en los nodos específicos en la jerarquía del modelo
            mesh.traverse(function(node){
                
                switch (node.name) {
                    case 'Cabellos':
                        activeMesh(node, userPrefs.ropa.Cabello);
                      break;
                    case 'Camisas':
                        activeMesh(node, userPrefs.ropa.Camisa);
                      break;
                    case 'Pantalones':
                        activeMesh(node, userPrefs.ropa.Pantalones);
                      break;
                    case 'Zapatos':
                        activeMesh(node, userPrefs.ropa.Zapatos);
                      break;
                }

                if (node.material) {
                    if(node.material.name === "Piel_Color"){
                        node.material.color.set(userPrefs.colorPiel);
                    }
                    if(node.material.name === "Cejas_Color"){
                        const colorCabello = userPrefs.ropa.Cabello.opciones.find(el => el.mesh ===userPrefs.ropa.Cabello.meshSelected).color;
                        node.material.color.set(colorCabello);
                        cejas_material = node.material;
                    }
                    if(node.material.name === "Cabello_Color"){
                        cabello_material = node.material;
                    }

                    const matInUserPrefs = userPrefs.ropa.materiales.find(mat => mat.material === node.material.name);

                    
                    
                    // console.log('mat', matInUserPrefs);
                    if(matInUserPrefs){
                        // console.log('mat', matInUserPrefs);
                        node.material.color.set(matInUserPrefs.color);
                        
                        //  Verifico si existen texturas para ese material, con el array creado antes
                        const textures = material_textures_data.find(text => text.material ===  node.material.name);
                        if( textures ){
                            if(textures.texturas.base){
                                node.material.map = textures.texturas.base;
                                node.material.needsUpdate = true;
                            }
                            if(textures.texturas.normal){
                                node.material.normalMap = textures.texturas.normal;
                            }
                        }
                    }
                }

            });

        });

        const activeMesh = (meshGroup, dataGroup) => {

            if(meshGroup.children && meshGroup.children.length > 0){
                meshGroup.children.forEach(el => el.visible=false);
    
                const mesh = meshGroup.children.find(el => el.name == dataGroup.meshSelected);
                if(mesh){
                    mesh.visible = true;
                }
            }
            
        }
    }

    
    
});