import React from 'react';
import { styled, useTheme } from '@mui/material/styles';

import Box from '@mui/material/Box';
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography';


//icons

import FolderRoundedIcon from '@mui/icons-material/FolderRounded';
import ArrowBackRoundedIcon from '@mui/icons-material/ArrowBackRounded';
import DeleteRoundedIcon from '@mui/icons-material/DeleteRounded';

import { changePubliImage } from '../../../aframe-components/AdminComponents/changeMat';


export default function MainPubli(props) {
    const [imageData, setImageData] = React.useState();

    const styleButton = {
        height: "fit-content",
        padding: 0,
        border: "none",
        backgroundColor: "transparent",
        cursor: "pointer",
        display: "flex",

    }

    const backDivStyle = {
        display: "flex",
        height: "100%",
        width: "15%",

        padding: '2rem',
    }


    const myStyle3 = {
        display: "flex",
        height: "100%",
        width: "30%",
        padding: '1rem 1rem'
    }

    const myStyleValla = {
        display: "flex",
        height: "100%",
        with: "25%",
        padding: '1rem',
        justifyContent: "space-around",
        alignItems: "center"

    }

    const myStyleValla2 = {
        display: "flex",
        height: "100%",
        width: "30%",

        padding: '1rem'
    }

    React.useEffect(() => {

        
            const image_input = document.getElementById("image-publi-input");
            
            image_input.addEventListener("change", function() {
            const reader = new FileReader();
            reader.addEventListener("load", () => {
                const uploaded_image = reader.result;
                
                
                setImageData(uploaded_image)
                changePubliImage(props.idPubli,uploaded_image)
                //console.log(uploaded_image)
                /* const imageDiv = document.querySelector("#display-image")
                imageDiv.style.backgroundImage = `url(${uploaded_image})`;
                imageDiv.style.display = 'inline-block' */
            });
            reader.readAsDataURL(this.files[0]);
            });
        
        
    
      },[]);

    

    return (
        <Box sx={{ width: "100%", height: "100%", display: "flex" }}>
                    <Box sx={backDivStyle}>
                        <button style={styleButton} onClick={props.handleClickBack}>
                            <ArrowBackRoundedIcon sx={{ mr: 1, color: "white" }}></ArrowBackRoundedIcon>
                            <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Volver</Typography>
                        </button>


                    </Box>
                    <Divider orientation="vertical" variant="middle" flexItem />
                    <Box sx={myStyleValla}>

                        <Box sx={{ textAlign: 'center' }}>
                            <Typography sx={{ textAlign: 'center', color: 'white' }} variant='h6'>Valla publicitaria</Typography>
                            <Typography sx={{ color: 'white' }} variant='h3'>{props.idPubli}</Typography>
                        </Box>

                    </Box>
                    <Divider orientation="vertical" variant="middle" flexItem />
                    <Box sx={myStyle3}>
                        <Box sx={{ width: "40%" , height:"100%" , backgroundColor: "white" }}>
                            
                            <img src={imageData} style={{ width: "100%", height: "100%", objectFit: "contain" }}></img>

                        </Box>
                        <Box sx={{ width: "60%", height: "100%", paddingLeft:"0.5rem" }}>
                            <Box sx={{ width: "100%"}}>
                                <Typography sx={{ color: 'white' }} variant='h6'>Imagen</Typography>
                            </Box>
                            <Box>
                                <button className="o-button-style custom-input-file" style={{ display: "flex", height: "auto", marginLeft: "0.2rem" }}>
                                    <FolderRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                    <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Elegir archivo</Typography>
                                    <input type="file" id="image-publi-input" className="input-file" accept="image/jpeg, image/png, image/jpg">
                                    </input>
                                </button>

                                <Box sx={{ display: "flex", marginTop: "0.5rem", marginLeft: "0.2rem" }}>
                                    <button className="o-button-style" style={{ display: "flex", height: "auto", marginLeft: "0.2rem" }}
                                        onClick={() => {
                                            setImageData("")
                                            changePubliImage(props.idPubli,"")
                                        }}>
                                        <DeleteRoundedIcon sx={{ mr: 1, color: "white", fontSize: "0.8rem" }} />
                                        <Typography sx={{ color: 'white', fontSize: "0.8rem" }}>Eliminar logo</Typography>
                                    </button>

                                </Box>
                            </Box>
                        </Box>

                    </Box>
                    <Divider orientation="vertical" variant="middle" flexItem />
                    <Box sx={myStyleValla2}>
                        <Box sx={{ width: "30%" }}>
                            <Typography sx={{ color: 'white' }} variant='h6'>URL</Typography>
                            <input className="o-input" type="text"></input>
                        </Box>
                        {/* <Box sx={{ width: "45%", marginLeft: "0.5rem" }}>
                            <Typography sx={{ color: 'white' }} variant='h6'>Descripción</Typography>
                            <input className="o-input" type="text" style={{ height: "3rem", width: "100%" }}></input>
                        </Box> */}

                    </Box>
                </Box>
    );
}